<template>
    <div id="bar">
        <router-link to="/foo">To Foo</router-link>
        <router-link to="/">To Home</router-link>
        <p>This is {{msg}} page!</p>
    </div>
</template>

<script>
export default {
  name: 'bar',
  data () {
    return {
      msg: 'bar~'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #42b983;
}
.router-link-active{
    color: red;
}
</style>



