<template>
    <div id="foo">
        <router-link to="/bar">To Bar</router-link>
        <router-link to="/">To Home</router-link>
        <p>This is {{msg}} page!</p>
    </div>
</template>

<script>
export default {
  name: 'foo',
  data () {
    return {
      msg: 'foo~'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #42b983;
}
.router-link-active{
    color: red;
}
</style>



