import Vue from 'vue'
import Router from 'vue-router'

// import Hello from '@/components/Hello'
import Bar from '../components/Bar'
import Foo from '../components/Foo'

import Home from '@/views/Home'
import MovieTab from '@/views/Movie/Tab'
import About from '@/views/About'
import MovieSearch from '@/views/Movie/Search'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'Hello',
    //   component: Hello
    // },
    {
      path: '/bar',
      name: 'Bar',
      component: Bar
    },
    {
      path: '/foo',
      name: 'Foo',
      component: Foo
    },
    {
      path: '/',
      redirect: '/movieHome',
      name: 'Home',
      component: Home
    },
    {
      path: '/movieHome',
      name: 'MovieHome',
      component: MovieTab
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/movieSearch',
      name: 'MovieSearch',
      component: MovieSearch
    }
  ]
})




