<template>
    <div id="tab">    
        <mu-drawer class='drawer' :open="open" :docked="docked" @close="toggle()">
            <div class="drawer-pic" style="background:#474a4f; padding: 70px 0;text-align:center; color:#fff;">
                <div class="title" style="font-size: 32px;">微豆 Vdo</div>
                <div class="description" style="font-size: 16px;color:#e0e0e0;">一个移动端豆瓣 Web 应用</div>
            </div>
            <mu-list value="movieHome" @change="page">
                <mu-list-item value="movieHome" title="电影">
                    <mu-icon slot="left" value="movie"/>
                </mu-list-item>
                <mu-list-item value="book" title="图书 - 敬请期待">
                    <mu-icon slot='left' value="book" />
                </mu-list-item>
                <mu-list-item value="music"	title="音乐 - 敬请期待">
                    <mu-icon slot='left' value="music_note" /> 	
                </mu-list-item>
                    <mu-divider shallowInset/>
                <mu-list-item value="about" title="关于">
                    <mu-icon slot='left' value="info" />
                </mu-list-item>
            </mu-list>
        </mu-drawer>
        <mu-paper :zDepth="2">
            <mu-appbar title='电影'>
                <mu-icon-button icon='menu' slot="left" @click="toggle(true)"/>
                <mu-icon-button icon='search' slot='right' @click="gotoSearch()"/>
            </mu-appbar>
            <mu-tabs :value="activeTab" @change="handleTabChange">
                <mu-tab value="in_theaters" @active="handleActive" title="正在热映" @click="getData('in_theaters')"/>
                <mu-tab value="coming_soon" @active="handleActive" title="即将上映" @click="getData('coming_soon')" />
            </mu-tabs>
        </mu-paper>
        <div v-if="activeTab === 'in_theaters'">
			<br>正在热映的~<br>
			此处应有电影列表(○´･д･)ﾉ
            <div class='wrap'>
				<mu-row class='paper-row'>
					<mu-col class='paper-elem' width="33" tablet="25" desktop="20" v-for="subject in subjects" :key="subject.id">
						<mu-paper>
							<div class='paper-border'>
								<div class='paper-img' :style="'background-image: url('+subject.images.large+')'">
								</div>
							</div>
							<div class='paper-text'>
								<p> {{subject.title}} </p>
								<p class="star-box">
									<span class="paper-star" v-if="subject.rating.average > 0">
										<Star class="star" :rating='subject.rating.average'></Star>
									</span>
									<span class='paper-rating'>{{subject.rating.average | fixNum}} </span>
								</p>
							</div>
						</mu-paper>
					</mu-col>
				</mu-row>
			</div>
        </div>
        <div v-if="activeTab === 'coming_soon'">
			<br>即将上映的~<br>
			此处应有电影列表(○´･д･)ﾉ
            <div class='wrap'>
				<mu-row class='paper-row'>
					<mu-col class='paper-elem' width="33" tablet="25" desktop="20" v-for="subject in subjects" :key="subject.id">
						<mu-paper>
							<div class='paper-border'>
								<div class='paper-img' :style="'background-image: url('+subject.images.large+')'">
								</div>
							</div>
							<div class='paper-text'>
								<p> {{subject.title}} </p>
								<p class="star-box">
									<span class="paper-star" v-if="subject.rating.average > 0">
										<Star class="star" :rating='subject.rating.average'></Star>
									</span>
									<span class='paper-rating'>{{subject.rating.average | fixNum}} </span>
								</p>
							</div>
						</mu-paper>
					</mu-col>
				</mu-row>
			</div>
        </div>
    </div>
</template>
<script>
	import Star from '@/components/Star';
	import axios from 'axios';
    export default {
		components: {
			Star,
		},
        data() {
			return {
				open: false,
				docked: true,
				activeTab: 'in_theaters',
				subjects: {}
			}
		},
		methods: {
			toggle(flag) {
				this.open = !this.open
				this.docked = !flag
			},
			handleTabChange(val) {
				this.activeTab = val
			},
			handleActive() {
				// window.alert('tab active')
				window.console.log('tab active')
			},
			page(val) {
				if (val === 'movieHome' || val === 'about') {
					this.$router.push({
						path: '/' + val
					});
					this.open = false;
				}
			},
			gotoSearch() {
				this.$router.push({
					name: 'MovieSearch'
				});
			},
			getData(type) {
				axios.get('/static/' + type + '.json')
				.then((res) => {
					this.subjects = res.data.subjects;
					window.console.log(res.data.subjects);
				}).catch((err) => {
					console.log(err);
				});
			}
		},
		created() {
			this.getData('in_theaters');
		}
    }
</script>
<style lang="less" scoped>
    #tab {
        width: 100%;
        height: 100%;
    }
    
    .drawer {
        color: #212121;
        background: #f5f5f5;
    }
		.wrap{
  padding: 6px;
}
	.wrap>.paper-row{
		justify-content: flex-start;
		flex-flow: row wrap;
	}
	.paper-elem{
		padding: 6px;
	}
	.paper-border{
		position: relative;
		border-radius: 2px 2px 0 0;
		overflow: hidden;
	}
	.paper-img{
		position: relative;
		width: 100%;
		padding-top:141%;
		background-size: cover;
		-moz-background-size: cover;
	}
	.paper-img-wrap{
		top:0;
		left: 0;
		position: absolute;
		height: 100%;
		width: 100%;
		border-radius: 2px 2px 0px 0px;
		box-sizing: border-box;
	}
	.paper-img-wrap>img{
		width: 100%;
	}
	.paper-text{
		padding: 6px 0 2px 0;
	}
	.paper-text>p{
		margin: 0;
		padding: 0 6px;
		font-size: 14px;
		height: 18px;
		line-height: 16px;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
		text-align: left;
	}
	.star-icon{
		font-size: 14px;
		margin-right: -4px;
		float: left;
	}
		.star-box{
		display: flex;
		color: #ff6f00;
	}
		.star{
		letter-spacing: -3px;
	}
		.paper-star{
		margin-right: 3px;
	}
		.paper-rating{
		position: relative;
	}
</style>


