<template>
  <div id='search'>
      <mu-paper class="demo-paper" :zDepth="1">
          <mu-appbar class='search-bar'>
              <mu-icon-button class='bar-icon' icon='arrow_back' slot="left" @click="gotoTab()"/>
              <input v-model.trim="q" :value="''" @keyup.enter="search" autofocus=true :underlineShow='true' class="bar-text" placeholder="请输入关键字"/>
              <mu-icon-button @click="clearText()" class='bar-icon' icon='close' slot="right" />
          </mu-appbar>
      </mu-paper>
      <div id="result" v-if="isSearching">
          <a href="javascipt:;" v-for="subject in subjects"  :key="subject.id">
              <mu-paper class="elem" :zDepth="1">
                  <div class="image" :style="'background-image: url('+subject.images.large+')'"></div>
                  <div class="text">
                      <div class="name">
                        <span class="title">{{subject.title}}</span>
                        <span class="year">{{subject.year}}</span>
                      </div>
                      <div class="star">
                          <Star class='star-box' :rating="subject.rating.average" v-if="subject.rating.average > 0"></Star>
                          <div class="star-num">{{subject.rating.average | fixNum}}</div>
                      </div>
                      <div class="genres">类型：{{subject.genres | arr2string}}</div>
                      <div class="director">导演：{{subject.directors.map(e=>e.name) | arr2string}}</div>
                      <div class="cast">主演：{{subject.casts.map(e=>e.name) | arr2string}}</div>
                  </div>
              </mu-paper>
          </a>
      </div>
      <!--<div id="result" v-if="isSearching">
          {{r}}
      </div>-->
  </div>
</template>
<script>
  import Star from '@/components/Star';
  import axios from 'axios';
  export default {
    name: 'movieSearch',
    components: {
			Star,
		},
    data () {
      return {
        q: '',
        isSearching:false,
        r: '',
        subjects: {}
      }
    },
    methods: {
      gotoTab() {
        this.$router.go(-1);
      },
      search() {
        // window.alert('searching...');
        this.isSearching = true;
        // this.r = '正在搜索~\n 请等待几分钟~#^_^';
        // var self = this;
        // setTimeout(function(){
        //   self.r = 'sorry，搜索不到相关数据~'
        // },3000)
        axios.get('/static/search_result.json')
				.then((res) => {
					this.subjects = res.data.subjects;
					window.console.log(res.data.subjects);
				}).catch((err) => {
					console.log(err);
				});
      },
      clearText() {
        this.q = '';
        this.isSearching = false;
      }
    }
  }
</script>
<style lang="less" scoped>
#search{
  top: 0;
  width: 100%;
  left: 0;
  .search-bar{
      background: #fff;
      position: relative;
  }
  .bar-icon{
      color:#757575;
  }
}
.bar-text{
    font-size: 16px;
    height: 100%;
    position: relative;
    margin-bottom: 0px;
    border: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    &:focus{
      border: none;
      -moz-appearance: none;
      -webkit-appearance: none;
      outline: none;
    }
}
#result{
    position: relative;
    box-sizing: border-box;
    width: 100%;
    min-height: 400px;
    text-align: left;
    .elem{
        position: relative;
        box-sizing: border-box;
        margin: 10px 15px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        height: 30%;
        overflow: hidden;
        .image{
            top: 0;
            left: 0;
            width: 30%;
            height: 0;
            background: #ccc;
            padding-top: 42%;
            background-size: cover;
            -moz-background-size: cover;
        }
        .text{
            overflow: hidden;
            top: 0;
            position: absolute;
            display: block;
            width: 70%;
            height: 100%;
            margin: 0;
            padding: 10px;
            font-size: 14px;
            right: 0;
            box-sizing: border-box;
            .name{
              height:20px;
              line-height: 20px;
              .title{
                font-size: 16px;
                font-weight: 800;
              }
              .year{
                color: #999;
              }
            }
            .star{
              display:-webkit-box;
              display:-ms-flexbox;
              display:flex;
              padding-top: 2px;
              color: #ff6f00;
              .star-box{
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                margin-right: 4px;
                letter-spacing: -3px;
                .star-icon{
                  margin-right: -3px;
                  padding: 2px 0;
                }
              }
              .star-num{
                margin-left: 0px;
              }
            }
            .cast{
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
        }
    }
}
</style>


